
.items_card {
	background-color: #FFFFFF;
	display: flex;
	justify-content: center;
	gap: 30px;

	.card {
		display: grid;
		gap: 10px;
		position: relative;


		.card_image {
			max-width: 354px;
			height: 441px;
			overflow: hidden;
			position: relative;

			a, img {
				display: block;
				width: 100%;
				height: 100%;
				object-fit: cover; // Обрізає зображення, зберігаючи пропорції
				transition: opacity 0.5s ease-in-out;

				&:hover {
					opacity: 0.8;
				}
			}

		}

		.view-more {
			position: absolute;
			bottom: 0;
			width: 100%;
			padding: 20px 0;
			background: rgba(255, 255, 255, 0.8);
			color: #3A3836;
			font-family: 'Poppins', sans-serif;
			font-size: 16px;
			text-align: center;
			opacity: 0;
			transition: opacity 0.5s ease-in-out;
			pointer-events: none; // щоб не заважав hover'у
		}

		&:hover .view-more {
			opacity: 1;
		}

		.card_title {
			color: #3A3836;
			text-align: center;
			font-family: 'Poppins', sans-serif;
			font-size: 16px;
			font-style: normal;
			font-weight: 300;
			line-height: 24px; /* 150% */
			letter-spacing: 0.32px;
			text-decoration: none;
			transition: opacity 0.5s ease;

			&:hover {
				opacity: 0.7;
			}

		}

		.tax {
			color: #3A3836;
			text-align: center;
			font-family: 'Poppins', sans-serif;
			font-size: 16px;
			font-style: normal;
			font-weight: 600;
			line-height: 24px; /* 150% */
			letter-spacing: 0.32px;
		}
	}

}

.catalog_main .items_card {
	display: grid !important;
	grid-template-columns: repeat(3, 1fr);
	gap: 30px;
}


@media (max-width: 1280px) {
	.items_card {
		display: grid;
		grid-template-columns: repeat(2, 1fr);

		gap: 15px;

	}
	.items_card .card .card_image {
		width: 100% !important;
	}

	.items_main {
		display: grid !important;
		grid-template-columns: repeat(4, 1fr);

		gap: 15px !important;

	}
}

@media (min-width: 1281px) {
	.items_main .card .card_image {
		max-width: 354px;
	}
}

@media (max-width: 1199px) {
	.items_main {
		grid-template-columns: repeat(2, 1fr);
		}


}


